*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

img {
  -o-object-fit: contain;
     object-fit: contain;
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.btn {
  display: -webkit-box;
